.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20%;
    height: 100%;
    background-color: rgb(253, 253, 253);
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    opacity: 0;
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    overflow: scroll;
    h2 {
        padding: 2rem;
    }
}

.Library-song {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 1rem 2rem 1rem 2rem;
    img {
        width: 30%;
    }
    &:hover {
        background-color: #bbe9ff;
        transition: all 0.5s ease;
    }
}

.song-discription {
    padding-left: 1rem;
    h3 {
        font-size: 1rem;
    }
    h4 {
        font-size: 0.7rem;
    }
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(155, 155, 155, 05) transparent;
}

*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba(155, 155, 155, 0.5);
    border-radius: 20px;
    border: transparent;
}

.selected {
    background-color: rgb(173, 241, 95);
}

.active-library {
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library {
        width: 100%;
    }
}